@font-face {
    font-style: normal;
    font-weight: normal;
}

.card-section {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.header {
display: flex;
justify-content: center;
align-items: center
}

.title {
font-family: 'Khand', sans-serif;
font-size: 1.8em;
padding-left: 16px;
}

.card {
  font-family: 'Lato', sans-serif;
  color: rgba(0,0,0,.7);
  font-size: 0.8em;
  overflow: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
  line-height: 1em; 
  background-color: rgba(29,161,242,0.40);
}

.search-box {
font-family:"Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size:13px;
    height:46px;
    margin-bottom:0;
    padding:0 15px;
    -webkit-border-radius: 5px;
    -moz-border-radius:5px;
    border-radius:5px;
}
